import React from 'react';
import { isAndroid, isIOS } from 'react-device-detect';
import { ImageLogo, ImageMain } from '../../assets';
import { openStore } from '../../utils/openStore';
import {
  StDownloadButton,
  StMainLink,
  StMainWrapper,
  StMainText,
  StStrongText,
  StLinkWrapper,
  StPlatformWrapper,
  StPlatformButton,
} from './styles';

function MainPage() {
  return (
    <StMainWrapper>
      <ImageLogo width='248px' />
      <StMainText>
          , <StStrongText></StStrongText>
      </StMainText>
      <ImageMain />
      <StLinkWrapper>
        <StMainLink to='/about'> !</StMainLink>
        <StMainLink to='/game'>  ()</StMainLink>
      </StLinkWrapper>
      {isAndroid || isIOS ? (
        <StDownloadButton
          onClick={() => openStore(isAndroid ? 'android' : 'iOS')}
        >
            
        </StDownloadButton>
      ) : (
        <StPlatformWrapper>
          <StPlatformButton onClick={() => openStore('iOS')}>
            iOS  
          </StPlatformButton>
          <StPlatformButton onClick={() => openStore('android')}>
              
          </StPlatformButton>
        </StPlatformWrapper>
      )}
    </StMainWrapper>
  );
}

export default MainPage;
